
body {
  width: 360px;
}
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
#pesquisar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
button {
  border: 1px solid #3079ed;
  margin-left: 10px;
  background-color: #4d90fe;
  color: white;
}




























